.form {
    display: flex;
    flex-direction: column;
    margin: auto auto;

    &-input {
        width: 343px;
        padding: 16px 12px;
        border-radius: 10px;
        border: none;
        background-color: rgba(189, 189, 189, 0.3);
        font-size: 16px;
        margin-bottom: 16px;

        &:focus-visible {
            outline: none;
            border: 1px solid #212121;
            padding: 15px 12px;
        }
    }

    &-button {
        margin-top: 16px;
        font-weight: 500;
        font-size: 14px;
        color: #fff;
        padding: 20px 0;
        border-radius: 10px;
        border: none;
        background-color: #3F51B5;
        cursor: pointer;
        text-align: center;
        text-decoration: none;
        letter-spacing: 0.0125em;
    }
}
